<template>
    <div class="editor-toolbar" :style="styles" v-show="isShow">
        <div class="editor-toolbar-content">
            <span class="editor-toolbar__label">缩放</span>
            <span class="editor-toolbar__zoom">
                <el-slider v-model="zoom" :min="5" :max="200" :format-tooltip="formatZoom"></el-slider>
            </span>
            <span @click="okHandler" class="editor-toolbar__ok btn-tip" aria-label="确定">
                <vx-icon name="ok"/>
            </span>
            <span @click="cancelHandler" class="editor-toolbar__cancel btn-tip" aria-label="取消">
                <vx-icon name="cancel"/>
            </span>
        </div>
    </div>
</template>
<script>
import {
    getter,
    mitation
} from 'src/design/store';
export default {
    name: 'vx-editor-toolbar',
    computed:{
        zoom: {
            get(){
                return getter.cutZoom();
            },
            set(val){
                mitation.setCutZoom(val);
            }
        },
        isShow(){
            return getter.cutable();
        },
        styles(){
            if(!this.isShow) return {}
            return {
                top: `${getter.toolbarPos().top - 45}px`,
                left: `${getter.toolbarPos().left}px`
            }
        }
    },
    methods:{
        formatZoom(val){
            return `${val}%`
        },
        okHandler(){
            mitation.setCutOk();
        },
        cancelHandler(){
            mitation.setCutCancel();
        }
    }
}
</script>
<style lang="stylus">
.editor-toolbar
    position absolute
    background #fff;
    // width 200px
    // height 30px
    box-shadow 0 0 5px #ccc
    border-radius 5px
    z-index 10
    .editor-toolbar-content
        width 100%
        height 100%
        display flex
        padding 2px 3px
        line-height 2rem
        color #666
.editor-toolbar__label
    width 3rem
    cursor default
    user-select none
    text-align center
    
.editor-toolbar__zoom
    position relative
    flex 1
    width 150px
    padding 0 5px
    &::after
        position absolute
        right 0
        top 50%
        transform translateY(-50%)
        content ''
        display block
        width 0
        height 1rem
        border-left 1px solid #ccc
    .el-slider__runway
        margin 11px 0
.editor-toolbar__ok
    width 2rem
    text-align center
    cursor default
.editor-toolbar__cancel
    width 2rem
    text-align center
    cursor default
</style>