import Vue from 'vue'
import Router from 'vue-router'
import GoodsList from '../page/GoodsList'
import modelCenter from '../page/modelCenter'
import bxShowPreview from '../page/bxShowPreview'

import Mall from '@/page/mall'
import Design from '../design'
import User from '@/user'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Mall,
      children: [{
        path: '',
        name: 'GoodsList',
        component: GoodsList
      }]
    },
    {

      path: '/mall',
      component: Mall,
      children: [{
        path: '',
        name: 'GoodsList',
        component: GoodsList
      },
      {
        path: 'discover',
        name: 'discover',
        component: GoodsList
      },
      {
        path: 'modelCenter',
        name: 'modelCenter',
        component: modelCenter
      },
      {
        path: 'bxShowPreview',
        name: 'bxShowPreview',
        component: bxShowPreview
      }
      ]
    }, {
      path: '/design',
      name: 'design',
      component: Design
    }, {
      path: '/user',
      component: User,
      redirect: '/myworks',
      children: [
        {
          path: '/myworks',
          name: 'myWorks',
          meta: {
            title: '我的作品'
          },
          component: () => import('../user/pages/myWorks')

        },
        {
          path: '/mydatas',
          name: 'myDatas',
          meta: {
            title: '数据中心'
          },
          component: () => import('src/user/pages/index'),
          children: [{
            path: '/basedata',
            name: 'baseData',
            meta: {
              title: '基础数据'
            },
            component: () => import('../user/pages/myDatas'),
          }]
        },
        {
          path: '/makeHistory',
          name: 'makeHistory',
          meta: {
            title: '作图记录'
          },
          component: () => import('../user/pages/makeHistory')

        },
        {
          path: '/myCollect',
          name: 'myCollect',
          meta: {
            title: '我的收藏'
          },
          component: () => import('../user/pages/myCollect')

        },
        {
          path: '/myBuy',
          name: 'myBuy',
          meta: {
            title: '我的购买'
          },
          component: () => import('../user/pages/myBuy')

        },


      ]
    },
    {
      path: '*',
      redirect: '/mall'
    }
  ]
})
