<template>
  <vx-template class="design-resource-element">
    <template v-slot:label>
      <h2>图形</h2>
    </template>
    <div class="design-resource-scene">
      <vx-loadmore
        :loading="loading"
        :offset="0"
        :nomore="noMore"
        :initScroll="true"
        @infinite-scroll="getDataHandler()"
      >
        <div class="design-resource-element-list">
          <ul>
            <li @click="selectHandler(item.id)" v-for="(item, index) in templateList" :key="index">
              <img class="design-resource-element__list__item__preview" :src="item.typefacePath" />
            </li>
          </ul>
        </div>
      </vx-loadmore>
    </div>
  </vx-template>
</template>
<script>
import VxTemplate from '../template'
// import VxSceneData from './sceneData'
import { API, log } from 'src/design/utils'
import VxLoadmore from 'src/design/components/loadmore'
export default {
  name: 'vx-element-scene',
  components: {
    VxTemplate,
    VxLoadmore
    // VxSceneData
  },
  data() {
    return {
      templateList: [],
      loading: false,
      pageNum: 0,
      totalPages: 10
    }
  },
  computed: {
    noMore() {
      //当起始页数大于总页数时停止加载
      return this.pageNum >= this.totalPages
    }
  },
  mounted() {
    this.getDataHandler()
  },
  methods: {
    getDataHandler() {
      let params = {
        page: this.pageNum, // 当前页
        limit: 10, // 页大小
        type: 'img' // 类别id
      }
      this.$axios
        .get(API.EFFECTS_API, { params: params })
        .then(data => {
          this.totalPages = data.data.pages
          this.loading = false
          this.templateList = this.templateList.concat(data.data.list)
          this.pageNum++
        })
        .catch(err => {
          log.danger(err)
        })
    },
    selectHandler(id) {
      let params = {
        id: id
      }
      this.setTextPositionHandler()
      this.$axios
        .get(API.EFFECT_DETAIL_API, { params })
        .then(data => {
          const detail = JSON.parse(data.data.info.detail)
          data.left = this.l
          data.top = this.t
          mitation.addImage(detail.model)
          // this.addHandler()
        })
        .catch(err => {
          log.danger(err)
        })
    },
    setTextPositionHandler() {
      if (getter.canvasH() - 80 < this.t) {
        this.l += 40
        this.t = 0
      }
      this.t += 40
      this.l += 40
      if (getter.canvasW() - 80 < this.l) {
        this.t = 0
        this.l = 0
      }
    }
  }
}
</script>
<style scoped>
.design-resource__content {
  overflow: auto;
}
</style>