<template>
    <div class="scroll">
        <div class="scroll-data-loading" ref="dataLoading">
            <div class="scroll-head" ref="scrollHead">
                <!-- 下拉刷新 -->
            </div>
            <div class="scroll-body" ref="scrollBody">
                <slot/>
            </div>
            <div class="scroll-foot" ref="scrollFoot">
                <div class="load-loading" v-show="statusLoading">加载中...</div>
                <div class="load-nomore" v-show="nomore">已全部加载完</div>
            </div>
        </div>
    </div>
</template>
<script>
/**
 * 该组件支持PC端，移动端需要添加对touch的事件监听
 */
export default {
    name: 'vx-loadmore',
    props:{
        loading: {// 加载中
            type: Boolean,
            default: false
        },
        initScroll: {
            type: Boolean,
            default: false
        },
        offset: {
            type: Number,
            default: 0
        },
        nomore: {
            type: Boolean,
            default: false
        }
    },
    data(){
        return {
            loadingType: null,
            margin: {
                top: 0,
                bottom: 0
            }
        }
    },
    computed:{
        statusLoading(){
            return !this.nomore && this.margin.bottom >= this.offset
        }
    },
    mounted(){
        this.$nextTick(() => {
            this.init()
        });
    },
    methods:{
        init(){ // 初始化
            this.bindEvents();
            this.updateView();
            if(this.initScroll){
                this.handlerScroll();
            }
        },
        handlerScroll(){
            this.updateView();
            if(this.loading || this.nomore){
                return
            }
            if(this.margin.bottom >= this.offset){
                
                this.$emit('infinite-scroll');
            }
        },
        updateView(){
            let root = this.$el.getBoundingClientRect();
            let {top, height} = this.$refs.dataLoading.getBoundingClientRect();
            this.margin.top = top;
            this.margin.bottom = root.height + root.top - (height + top) + this.offset;
        },
        bindEvents(){
            this.$el.addEventListener('scroll', this.handlerScroll);
        }
    },
}
</script>
<style>
.scroll{
    width: 100%;
    height: 100%;
    overflow: auto;
}
.scroll-foot{
    text-align: center;
    font-size: 14px;
    color: #666;
    line-height: 20px;
    height: 20px;
}
</style>