<template>
  <div class="gdd-default-header__wrapper">
    <header class="gdd-default-header">
      <div class="gdd-default-header__nav">
        <div class="gdd-default-header__content">
          <a
            class="router-link-active gdd-default-link gdd-default-header__gd-logo"
            href="/"
            rel="nofollow"
            title="返回首页"
          >
            <div class="gdd-default-header__logo">
              <img src="https://dancf-cdn.oss-cn-hangzhou.aliyuncs.com/gaodingx/assets/logo.svg" />
            </div>
          </a>
          <div class="gdd-default-header__channel"></div>
          <div class="gdd-default-header__nav-list">
            <!-- <a class="gdd-default-link gdd-default-header__nav-item" href="/discover" rel>发现</a> -->
            <span class="g-popover__container gdd-default-header__nav-item">
              <router-link class="gdd-default-link" to="/mall/discover">发现</router-link>
            </span>
            <span class="g-popover__container gdd-default-header__nav-item">
              <!-- <a class="gdd-default-link" href="/templates"
              rel="">模板中心</a>-->
              <router-link class="gdd-default-link" to="/mall/modelCenter">模板中心</router-link>
              <!-- <span @click="jumpPage('/modelCenter')" rel>模板中心</span> -->
            </span>
          </div>
          <div class="gdd-default-header__right">
            <div style="flex: 1 1 auto"></div>
            <div v-if="$route.path!=='/mall/modelCenter'" class="gdd-header-search">
              <span class="g-popover__container">
                <form>
                  <div
                    class="g-input g-input--group g-input--append g-input--normal g-input--medium"
                  >
                    <input
                      type="search"
                      autocomplete="off"
                      placeholder="双十一"
                      maxlength="50"
                      class="g-input__inner"
                    />
                    <div class="g-input__append">
                      <svg class="g-icon g-icon--normal g-icon--gdd-camera">
                        <use xlink:href="#gdd-camera" />
                      </svg>
                      <svg class="g-icon g-icon--normal g-icon--gdd-search">
                        <use xlink:href="#gdd-search" />
                      </svg>
                    </div>
                  </div>
                </form>
              </span>
            </div>
            <!-- <img
                src="https://cdn.dancf.com/gaodingx/www/image/video2x_c9eb785e.png"
                srcset="https://cdn.dancf.com/gaodingx/www/image/video3x_8dd6639b.png 3x"
                alt="新手教程"
                class="gdd-default-header__novice"
            />-->
            <div class="gdd-header-user">
              <button type="button" class="g-btn g-btn--text gdd-header-user__nick">
                <!-- <span class="g-btn__content">登录/注册</span> -->
                <!-- <span class="el-dropdown-link g-btn__content">登录/注册</span> -->
                <span @click="showLogin = true" v-if="!isLogin" class="g-btn__content">登录/注册</span>

                <el-row v-if="isLogin" class="g-btn__content">
                  <el-row class="block-col-2">
                    <el-col :span="12">
                      <el-dropdown>
                        <span class="el-dropdown-link">
                          {{userName}}
                          <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                          <el-dropdown-item icon="el-icon-plus">作图记录</el-dropdown-item>
                          <el-dropdown-item icon="el-icon-circle-plus">收藏夹</el-dropdown-item>
                          <el-dropdown-item icon="el-icon-circle-plus-outline">订单中心</el-dropdown-item>
                          <el-dropdown-item icon="el-icon-check">账号设置</el-dropdown-item>
                          <el-dropdown-item icon="el-icon-user" @click.native="exitUser">退出登录</el-dropdown-item>
                        </el-dropdown-menu>
                      </el-dropdown>
                    </el-col>
                  </el-row>
                </el-row>
              </button>
            </div>
          </div>
        </div>
      </div>
    </header>
    <VxLogin @hideLogin="hideLoginFormHandler" v-if="showLogin && !isLogin" />
  </div>
</template>
<script>
import MyStorage from 'src/utils/cache'
import VxLogin from 'src/design/view/login'
export default {
  name: 'vx-head',
  data() {
    return {
      showLogin: false
    }
  },
  computed: {
    isLogin() {
      return this.$store.getters.getLoginState()
    },
    userName() {
      return MyStorage.getItem('userName')
    }
  },
  components: {
    VxLogin
  },
  created() {},

  methods: {
    hideLoginFormHandler() {
      this.showLogin = false
    },
    // 退出登录
    exitUser() {
      this.showLogin = false
      MyStorage.clear()
      this.$store.commit('updateLoginState', false)
    }
  }
}
</script>
<style lang="scss">
.vx-login-form-pass {
  margin-top: 20px;
}
.vx-login-form-msg {
  margin-top: 20px;
}
.vx-login-phone-login {
  margin-bottom: 5rem;
}
.vx-login-bottom {
  margin-top: 10px;
}
.vx-login-form-code {
  padding: 0;
}
.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.demonstration {
  display: block;
  color: #8492a6;
  font-size: 14px;
  margin-bottom: 20px;
}
</style>