<template>
  <div id="design-app">
    <vx-menu @redo="redoHandler" @undo="undoHandler"></vx-menu>
    <div class="design-main">
      <vx-aside @add="addHandler" />
      <vx-canvas />
      <vx-config
        @opDelete="deleteHandler"
        @moveUp="moveUpHandler"
        @moveDown="moveDownHandler"
        @moveTop="moveTopHandler"
        @moveBottom="moveBottomHandler"
        :data="selectWidget"
      ></vx-config>
    </div>
  </div>
</template>
<script>
import { mitation, getter } from 'src/design/store'
import { mixin } from 'src/design/utils'
import VxMenu from './menu'
import VxAside from 'src/design/components/aside'
import { VxCanvas } from './canvas'
import { API, log } from 'src/design/utils'
import VxConfig from './config'
// import {API} from '@/utils/API';
export default {
  name: 'vx-design-layout',
  mixins: [mixin],
  components: {
    VxAside,
    VxMenu,
    VxCanvas,
    VxConfig
  },
  data() {
    return {
      panelShow: -1
    }
  },
  computed: {
    isShow() {
      return this.panelShow !== -1
    },
    selectWidget() {
      return getter.current()
    }
  },
  created() {
    let id = this.$route.query.id
    if (id) {
      this.$axios
        .get(API.TEMPLATE_API, {
          params: {
            // user_id: '854466176'
            id: id,
            type: 'template'
          }
        })
        .then(data => {
          mitation.setTemplate(JSON.parse(data.data.info.detail), id)
          this.$emit('select')
        })
        .catch(err => {
          log.danger(err)
        })
    }
  },
  methods: {
    hidePanelHandler() {
      this.togglePanelHandler(-1)
    },
    // 切换侧边栏菜单
    togglePanelHandler(panel) {
      if (panel === this.panelShow) {
        this.panelShow = -1
      } else {
        this.panelShow = panel
      }
    },
    undoHandler() {
      mitation.setUndo()
    },
    redoHandler() {
      mitation.setRedo()
    },
    deleteHandler() {
      let selectOrg = {
        type: 'canvas',
        id: -1
      }
      mitation.setCurrent(selectOrg)
      //   this.selectWidget = Object.assign({}, selectOrg)
    },
    //向上一层
    moveUpHandler() {
      let up = this.selectWidget.over.up
      if (up.length > 0) {
        let id = up.shift()
        mitation.moveUp(this.selectWidget.id, id)
      }
    },
    //向下一层
    moveDownHandler() {
      let down = this.selectWidget.over.down
      if (down.length > 0) {
        let id = down.pop()
        mitation.moveDown(this.selectWidget.id, id)
      }
    },
    moveTopHandler() {
      mitation.moveTop(this.selectWidget.id)
      //   this.selectWidget.id = getter.layout().elements.length - 1;
      // mitation.setCurrent({
      //     id: getter.layout().elements.length - 1,
      //     type: this.selectWidget.type
      // })
    },
    moveBottomHandler() {
      mitation.moveBottom(this.selectWidget.id)
      //   this.selectWidget.id = 0;
      // mitation.setCurrent({
      //     id: 0,
      //     type: this.selectWidget.type
      // })
    },
    addHandler(data) {
      mitation.setCurrent({
        id: data.id,
        type: data.type
      })
    }
  }
}
</script>
