import Vue from 'vue'
import Vuex from 'vuex'
import MyStorage from 'src/utils/cache'
Vue.use(Vuex)
const state = {
	isLogin: MyStorage.getItem('Token') || false
}

const store = new Vuex.Store({
	state,
	getters: {
		getLoginState: (state) => () => {
			return state.isLogin
		}
	},
	mutations: {
		updateLoginState(state, val) {
			state.isLogin = val
		}
	}
})
export default store;
