<template>
  <vx-template class="design-resource-template">
    <template v-slot:label>
      <vx-icon name="menu" />
      <h2>模板</h2>
    </template>
    <template>
      <el-input
        size="large"
        class="design-resource-templet__input__search"
        placeholder="搜索模板"
        prefix-icon="el-icon-search"
        v-model="query"
        @keydown.enter="searchHandler"
        @change="inputHandler"
        @blur="searchHandler"
      />
      <!-- <div class="design-resource__scroll">
                <div class="design-resource__scroll__content">
                    <ul
                        class="design-resource-templet__list"
                        v-infinite-scroll="loadHandler"
                        :infinite-scroll-disabled="disabled">
                        <li
                        class="design-resource-templet__list__item"
                        v-for="(item, index) in templateList"
                        :key="index">
                            <div
                                class="design-resource-templet__list__item__preview"
                                @click="selectHandler(item.id)"
                                :style="{width: `${item.preview.width * 0.1}px`, height: `${item.preview.height * 0.1}px`,backgroundColor:item.preview.maincolor.hex}">
                                <img
                                :src="item.preview.url"
                                :style="{width: `${item.preview.width * 0.1}px`, height: `${item.preview.height * 0.1}px`}"/>
                            </div>
                        </li>
                    </ul>
                    <div class="design-resource__scroll__content__loading">
                        <p v-if="loading">加载中...</p>
                        <p v-if="noMore">没有更多了</p>
                    </div>
                </div>
      </div>-->
      <vx-loadmore
        :loading="loading"
        :nomore="noMore"
        :initScroll="true"
        @infinite-scroll="getDataHandler()"
      >
        <div class="design-resource__scroll__content">
          <ul class="design-resource-templet__list">
            <li
              class="design-resource-templet__list__item"
              v-for="(item, index) in templateList"
              :key="index"
            >
              <div
                class="design-resource-templet__list__item__preview"
                @click="selectHandler(item.id)"
                :style="{
                                  backgroundImage: `url(${item.picUrl})`
                                }"
              >
                <!-- :style="{width: `${item.preview.width * 0.1}px`, height: `${item.preview.height * 0.1}px`,backgroundColor:item.preview.maincolor.hex}"> -->
                <!-- <img :src="item.picUrl"/> -->
                <!-- :src="item.preview.url" -->
                <!-- :style="{width: `${item.preview.width * 0.1}px`, height: `${item.preview.height * 0.1}px`}" -->
                <!-- <div class="design-resource-templet__list__item__preview__buttons">{{index+1}}</div> -->
              </div>
            </li>
          </ul>
          <!-- <div class="design-resource__scroll__content__loading">
                        <p v-if="loading">加载中...</p>
                        <p v-if="noMore">没有更多了</p>
          </div>-->
        </div>
      </vx-loadmore>
    </template>
  </vx-template>
</template>
<script>
import { mitation } from 'src/design/store'
import VxTemplate from './template'
import VxLoadmore from 'src/design/components/loadmore'
import { API, log } from 'src/design/utils'

export default {
  name: 'vx-design-template',
  components: {
    VxTemplate,
    VxLoadmore
  },
  data() {
    return {
      query: '',
      templateList: [],
      loading: false,
      // noMore: false,
      pageNum: 1,
      totalPages: 2,
      search: false
    }
  },
  computed: {
    noMore() {
      //当起始页数大于总页数时停止加载
      return this.pageNum > this.totalPages
    }
  },
  methods: {
    inputHandler() {
      this.search = true
    },
    searchHandler() {
      if (this.search) {
        this.pageNum = 1
        this.templateList = []
        this.getDataHandler()
        this.search = false
      }
    },
    getDataHandler() {
      let params = {
        page: this.pageNum, // 当前页
        limit: 10, // 页大小
        category_id: 0, // 类别id
        query: this.query, // 搜索
        sort: '' // 排序
      }
      this.$axios
        .get(API.SEARCH_API, { params })
        .then(data => {
          this.totalPages = data.data.pages
          this.loading = false
          this.templateList = this.templateList.concat(data.data.list)
          this.pageNum++
        })
        .catch(err => {
          log.danger(err)
        })
    },
    selectHandler(id) {
      this.$axios
        .get(API.TEMPLATE_API, {
          params: {
            // user_id: '854466176'
            id: id,
            type: 'template'
          }
        })
        .then(data => {
          mitation.setTemplate(JSON.parse(data.data.info.detail), id)
          this.$emit('select')
        })
        .catch(err => {
          log.danger(err)
        })
    }
  }
}
</script>
