<template>
  <div>
    <modelSearch @keyWord="searchHandler"></modelSearch>
    <modelBanner ref="banner" @selectCatalog="getDataList"></modelBanner>
    <!-- @selectCatalog="selectCatalogHandler" -->
    <div class="gd-waterfall gdd-template-list gdd-search-page__search-list">
      <div class="infinite-list-wrapper" style="overflow:auto">
        <div class="list" v-infinite-scroll="load" :infinite-scroll-disabled="disabled">
          <div class="gd-waterfall-itema" :key="index" v-for="(item,index) in NewItems">
            <!-- 遮罩层 -->
            <div class="sbx-mask-star">
              <div>
                <!-- 预览图标 -->
                <div @click="jumpPage('/mall/bxShowPreview',item)" class="sbx-mask-star-left"></div>
                <i class="el-icon-view"></i>
                <!-- 收藏图标 -->
              </div>
              <div>
                <i
                  class="el-icon-star-on"
                  :class="{active: isSelected}"
                  @click="toggleCollect(index,item)"
                ></i>
              </div>
            </div>

            <div @click="goEditHandler(item.id)" class="mall-model-edit">
              <i class="el-icon-edit"></i>点击在线编辑
            </div>
            <div class="mask"></div>
            <div class="gdd-normal-template-card">
              <div class="gdd-normal-template-card__preview">
                <div tag="div" id="template.121376" class="gdd-template-preview">
                  <div></div>
                  <div
                    class="gd-lazy-image gd-lazy-image--loaded gd-lazy-image--transition gdd-template-normal-preview"
                    style="width: 224px; height: 398px;"
                  >
                    <img :src="item.picUrl" />
                    <div class="gd-lazy-image__bg"></div>
                  </div>
                </div>
              </div>

              <a
                href="/template/121376"
                class="gdd-normal-template-card__title"
                target="_blank"
                :title="item.name"
              >{{item.name}}</a>
            </div>
          </div>
        </div>
        <p v-if="loading">加载中...</p>
        <div class="mall-nomore" v-if="noMore">
          <p>
            <img src="../assets/noMoreDate.png" />
          </p>
          <p>没有更多数据了哦~~</p>
        </div>
      </div>
    </div>

    <!-- <li v-for="i in count" :key="i" class="list-item">{{ i }}</li> -->
  </div>
</template>

<script>
import modelSearch from './bxModelSearch'
import modelBanner from './bxModelBanner'

import { goodsList } from '../api/api'
export default {
  name: 'vx-bxModelShow',
  components: { modelBanner, modelSearch },
  data() {
    return {
      currentSubCategory: [],
      count: 10,
      loading: false,
      keyWord: '',
      isSelected: false
      // CollectionON: false //收藏星星  切换
    }
  },
  computed: {
    categoryId() {
      return this.$refs.banner.activeClass
    },
    noMore() {
      return this.count >= this.currentSubCategory.length
      // return this.count >= 20
    },
    disabled() {
      return this.loading || this.noMore
    },
    NewItems() {
      var that = this

      var NewItems = []

      this.currentSubCategory.map(function(item) {
        if (item.name.search(that.keyWord) != -1) {
          NewItems.push(item)
        }
      })

      return NewItems
    }
  },

  mounted() {
    this.getDataList()
  },
  methods: {
    jumpPage(Page, item) {
      this.$router.push({
        path: Page,
        query: {
          item: JSON.stringify(item)
        }
      })
    },
    toggleCollect(index, item) {
      JSON.parse(index) //打包专用
      JSON.parse(item) //打包专用
      this.isSelected = !this.isSelected
    },
    load() {
      this.loading = true
      setTimeout(() => {
        this.count += 2
        this.loading = false
      }, 2000)
    },
    getDataList() {
      let param = {
        // id: val,
        keyword: this.keyWord,
        page: 1,
        limit: 10,
        categoryId: this.categoryId
      }
      //分类目录当前分类数据接口
      goodsList(param).then(res => {
        this.currentSubCategory.length = 0
        this.currentSubCategory = [...res.data.list]
      })
    },
    searchHandler(val) {
      this.keyWord = val
      this.selectCatalogHandler()
    },
    goEditHandler(id) {
      this.$router.push({
        path: '/design',
        query: {
          id: id
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.list {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.gd-waterfall-itema {
  margin-left: 15px;
  position: relative;
  cursor: pointer;
}
.mask {
  width: 224px;
  height: 398px;
  position: absolute;
  border-radius: 4px;
  top: 0;
  left: 0px;
  z-index: 999;
  display: none;
}
.gd-waterfall-itema:hover .mask {
  z-index: 999;

  background: rgba(26, 21, 21, 0.3);
  display: block;
}
.gd-waterfall-itema:hover .sbx-mask-star {
  display: flex;
}
.gd-waterfall-itema:hover .mall-model-edit {
  display: block;
}
//收藏
.el-icon-star-on,
.el-icon-view {
  font-size: 26px;
  cursor: pointer;
}
.el-icon-star-on.active {
  color: red;
}
.sbx-mask-star {
  transition: 0.5s;
  top: 10px;
  width: 100px;
  height: 30px;
  border-radius: 8px;
  position: absolute;
  background: #fff;
  z-index: 1000;
  right: 10px;
  display: none;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
  div {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .sbx-mask-star-left {
    position: absolute;
    padding: 10px 25px;
    border-right: 1px solid #7b7b7b;
    border-width: thin;
  }
}
.gdd-normal-template-card__title {
  padding-left: 10px;
  box-sizing: border-box;
}
.infinite-list-wrapper {
  width: 100%;
}
.mall-nomore {
  text-align: center;
}
.mall-model-edit {
  position: absolute;
  z-index: 1000;
  padding: 8px 20px;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
  font-size: 12px;
  color: #fff;
  left: 10px;
  bottom: 65px;
  display: none;
}
</style>