<template>
  <div class="panel">
    <vx-design-template @select="SelectHandler" v-show="show === 0" />
    <vx-design-text @add="addHandler" @select="SelectHandler" v-show="show === 1" />
    <vx-design-element @add="addHandler" @select="SelectHandler" v-show="show === 2" />
    <vx-design-background v-show="show === 3" />
    <vx-upload v-show="show === 4" />
  </div>
</template>
<script>
import VxDesignTemplate from './designTemplate'
import VxDesignText from './designText'
import VxDesignElement from './designElement'
import VxDesignBackground from './designBackground'
import VxUpload from './upload'
export default {
  name: 'vx-panel',
  props: {
    show: Number
  },
  components: {
    VxDesignTemplate,
    VxDesignText,
    VxDesignElement,
    VxDesignBackground,
    VxUpload
  },
  methods: {
    SelectHandler() {
      this.$parent.selectHandler()
    },
    addHandler(data) {
      this.$parent.addHandler(data)
    }
  }
}
</script>