<template>
<div class="design-container" :class="[type]">
    <div class="design-canvas" :style="canvasStyles">
        <div class="editor-shell" :style="canvasStyles">
            <div class="editor-canvas" id="canvas">
                <div class="editor-layout">
                    <vx-Editor-bg 
                    :w="bgW"
                    :h="bgH"
                    :bgColor="bgColor"
                    :bgImg="bgImg"
                    :bgImgInfo="bgImgInfo"
                    :bgSize="bgSize"/>
                    <template v-for="(item, index) in layout.elements">
                        <vx-editor-text 
                        :index="index"
                        :zoom="zoom"
                        v-if="item.type==='text'" :data.sync="item" :key="item.id" />
                        <vx-editor-image 
                        :zoom="zoom"
                        :index="index"
                        v-if="['image', 'mask'].includes(item.type)" :data.sync="item" :key="item.id" />
                        <vx-editor-svg
                        :zoom="zoom"
                        :index="index"
                        v-if="['svg'].includes(item.type)" :data.sync="item" :key="item.id"
                        />
                        <vx-editor-group 
                        :zoom="zoom"
                        :index="index"
                        v-if="item.type==='group'" :data.sync="item" :key="item.id" />
                    </template>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import {getter} from 'src/design/store';

import VxEditorBg from './background';
import VxEditorText from './text';
import VxEditorImage from './image';
// import VxEditorMask from './mask';
import VxEditorGroup from './group';
import VxEditorSvg from './svg';
// const html2canvas = require('html2canvas');
// import html2canvas from 'html2canvas';

export default {
    name: "vx-canvas-preview",
    props:{
        height: Number, // 容器(父级)的高度
        width: Number, // 容器(父级)的宽度
        w: Number,
        h: Number,
        print: {
            type: Boolean,
            default: false
        },
        type: {
            validator: function(val){
                return ['preview', 'print'].includes(val);
            },
            default: 'preview'
        },
    },
    components:{
        VxEditorBg,
        VxEditorText, // 文本
        VxEditorImage, // 图片 
        // VxEditorMask, // mask
        VxEditorGroup,
        VxEditorSvg
    },
    data(){
        return{
            hoverStyle: {},
            transformStyle: {},
            output: null,
            timer: null,
            size: 0,
            zoom: this.type === 'print'? 1:0,
            // print: true
        }
    },
    computed:{
        global(){
            return getter.global();
        },
        layout(){
            return getter.layout();
        },
        canvasStyles(){
            return this.type ==='preview'? {
                width: `${this.w * this.size}px`,
                height: `${this.h * this.size}px`
            }: this.type === 'print'? {
                width: `${this.layout.width * this.zoom}px`,
                height: `${this.layout.height * this.zoom}px`
            }: {}
        },
        canvasW(){
            return this.w * this.size
        },
        canvasH(){
            return this.h * this.size
        },
        // 背景图层
        bgW(){return this.layout.width * this.zoom},
        bgH(){return this.layout.height * this.zoom},
        bgColor(){return  this.layout.backgroundColor || '#fff'},
        bgImg(){return  this.layout.backgroundImage},
        bgImgInfo(){return this.layout.backgroundImageInfo},
        bgSize(){return this.layout.backgroundSize},
        // bgRepeat(){return this.layout.backgroundRepeat}

    },
    mounted(){
        this.type ==='preview' && this.updateSizeHandler();
    },
    methods:{
        updateSizeHandler(){
            let zoomX = this.width/this.w;
            let zoomY = this.height/this.h;
            if(zoomX > zoomY){
                this.size = zoomY;
            }else{
                this.size = zoomX;
            }
            this.updateZoomHandler()
        },
        updateZoomHandler(){
            let zoomX = this.canvasW/this.layout.width;
            let zoomY = this.canvasH/this.layout.height;
            if(zoomX > zoomY){
                this.zoom = zoomY;
            }else{
                this.zoom = zoomX;
            }
        },
        async printImageHandler(){
            if(this.type === 'print'){
                const el = document.querySelector('#canvas');
                const options = {
                    type: "dataURL",
                    logging: false,
                    scale: 1,
                    // allowTaint:true,//允许加载跨域的图片
                    taintTest:true, //检测每张图片都已经加载完成
                    useCORS: true, //使用跨域
                    backgroundColor: null,
                };
                return await this.$html2canvas(el, options);
            }else{
                throw new Error('当前不是生成图片模式')
            }
            
        }
    },
    watch:{
        w: function(){
            this.type ==='preview' && this.updateSizeHandler();
        },
        h: function(){
            this.type ==='preview' && this.updateSizeHandler();
        }
    }
}
</script>