<template>
    <div v-if="!showWatermark" :style="styles" class="editor-watermark">
        <img :src="url" ref="image">
    </div>
</template>
<script>
import {getter} from 'src/design/store';
export default {
    name: 'vx-water-mark',
    data(){
        return{
            url: 'https://st0.dancf.com/csc/213/configs/system/20190503-205447-e721.png'
        }
    },
    computed:{
        showWatermark(){
            return getter.global().showWatermark;
        },
        zoom(){
            return getter.global().zoom;
        },
        styles(){
            return {
                width: `${642*this.zoom}px`,
                right: `${14*this.zoom}px`,
                height: `${137*this.zoom}px`,
                bottom: `${14*this.zoom}px`
            }
        }
    }
}
</script>