<template>
  <div>
    <div class="gdd-search-page__result-total">
      <!--<div class="gdd-search-page__all">全部模板 </div>-->
      <!---->
    </div>

    <div class="gdd-search-page__divider gdd-search-page__divider--head"></div>

    <div>
      <div class="gdd-tags gdd-search-page__tags">
        <span class="gdd-tags__label">分类</span>
        <div class="gdd-tags__group">
          <!-- <a href="/templates" class="router-link-exact-active router-link-active gdd-tags__item gdd-tags__item--active">
						全部
          </a>-->
          <!-- <span
            class="gdd-tags__item"
            :class="{'active':activeClass == null}"
            @click="getItem(null)"
          >全部</span>-->
          <span
            v-for="(v,index) in categoryList"
            :key="index"
            class="gdd-tags__item"
            :class="activeClass == index ? 'active':''"
            @click="getItem(index)"
          >{{categoryList[index].name}}</span>
        </div>
      </div>
      <div class="gdd-search-page__divider gdd-search-page__divider--bottom"></div>
    </div>
  </div>
</template>

<script>
import { catalogList } from '../api/api'

export default {
  name: 'vx-bxModelBanner',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      activeClass: 0, // 0为默认选择第一个，-1为不选择

      // 分类目录全部分类数据
      categoryList: []
    }
  },
  methods: {
    getItem(index) {
      // 把当前点击元素的index，赋值给activeClass
      this.activeClass = index
      this.$emit('selectCatalog')
      // this.getCurrentSubCategory();
    }
  },
  created() {
    //分类目录全部分类数据接口
    catalogList().then(res => {
      this.categoryList = res.data.categoryList
      // this.$emit('selectCatalog', this.categoryList[0].id)
      // this.getCurrentSubCategory();
    })
  }
}
</script>

<style scoped lang="scss">
.gdd-tags__item:hover {
  background: paleturquoise;
}
.active {
  background: #eee;
  color: #1e82d2;
  font-weight: bolder;
}
</style>