<template>
  <div class="design-menus">
    <div class="menu-logo">海报设计</div>
    <div class="menu-view">
      <div class="menu-tool">
        <vx-icon
          class="menu-undo"
          :class="{'disable':undoStatus}"
          @click.native="undoHandler"
          title="撤销"
          name="undo"
        />
        <vx-icon
          class="menu-redo"
          :class="{'disable':redoState}"
          @click.native="redoHandler"
          title="重做"
          name="redo"
        />
        <span class="menu-save">
          <vx-icon
            @click.native="showSaveItemHandler"
            aria-disabled
            :title="saveTitle"
            name="save"
          />
        </span>
        <div v-show="isSaveListShow" class="menu-save-item">
          <ul>
            <li @click="saveHandler">保存</li>
            <!-- <li @click.native="saveRecordHandler">保存作图记录</li> -->
          </ul>
        </div>
      </div>
      <div class="meun-zoom">
        <vx-icon class="menu-add" @click.native="zoominHandler" title="放大" name="add" />
        <div class="menu-zoomvalue">
          <div class="menu-zoomnum">{{zoomValue}}%</div>
          <vx-icon
            @click.native="zoomHandler"
            :title="zoomF?'适应屏幕':'实际大小'"
            class="menu-zoomfit"
            :name="`zoom-${zoomF?'fit':'off'}`"
          ></vx-icon>
        </div>
        <vx-icon class="menu-sub" @click.native="zoomoutHandler" title="缩小" name="sub" />
      </div>
      <div class="menu-edit">
        <div @click="showEditHandler" v-if="!isShowEdit" class="menu-nickname">
          <!-- <el-tooltip effect="dark" content="修改标题" placement="bottom"> -->
          <div class="btn-tip" aria-label="修改标题">
            <div class="menu-title">{{title}}</div>
            <vx-icon name="edit" />
          </div>
          <!-- </el-tooltip> -->
        </div>
        <div v-if="isShowEdit" :class="{'iseditshow': isShowEdit}" class="menu-nickname">
          <input
            ref="inputVal"
            maxlength="20"
            v-model="title"
            @blur="hideEditHandler"
            class="menu-title-edit"
          />
        </div>
      </div>
    </div>

    <div class="menu-operate">
      <!-- <div class="element-bar">
        <vx-icon name="save"></vx-icon>
        <vx-ico n name="save"></vx-ico>
        <vx-icon name="save"></vx-icon>
        <vx-icon name="save"></vx-icon>
      </div>-->
      <div @click="showLogin = true" class="menu-share">分享</div>
      <div @click="downloadHandler" class="menu-download">下载</div>
    </div>
    <VxDownload @close="hideDownloadHandler" v-if="isLogin && showDownload" />
    <VxLogin @hideLogin="hideLoginHandler" v-if="showLogin && !isLogin" />
    <vx-canvas-download type="print" ref="print" />
  </div>
</template>
<script>
import { getter, mitation } from 'src/design/store'
import util from 'src/design/utils'
import VxLogin from './login'
import VxDownload from './download'
import VxCanvasDownload from './canvas/canvasPreview'
import API from 'src/design/utils/API'
export default {
  name: 'vx-menu',
  mixins: [util.mixin],
  components: {
    VxLogin,
    VxDownload,
    VxCanvasDownload
  },
  data() {
    return {
      showLogin: false,
      isSaveListShow: false, //保存作图记录是否显示，默认不显示
      saveTitle: '保存', //保存按钮title
      isShowEdit: false, //显示编辑框
      title: '', //标题名称
      // zoomIcon: 'zoom-fit',
      // zoomTitle: '适应屏幕'
      zoomF: false,
      showDownload: false,
      zoom: 1
    }
  },
  computed: {
    zoomValue() {
      return util.floatObj.multiply(
        util.floatObj.multiply(getter.zoom(), 1, 2),
        100
      )
    },
    undoStatus() {
      return !getter.undoStatus()
    },
    redoState() {
      return !getter.redoStatus()
    },
    isLogin() {
      return this.$store.getters.getLoginState()
    }
  },
  created() {
    this.title = getter.title()
    setInterval(() => {
      this.saveHandler()
    }, 180 * 1000)
  },

  methods: {
    // 改变缩放比例，。。适应屏幕/实际大小
    zoomHandler() {
      // if (this.zoomIcon === 'zoom-off') {
      //   this.zoomIcon = 'zoom-fit'
      //   this.zoomTitle = '适应屏幕'
      // } else if (this.zoomIcon === 'zoom-fit') {
      //   this.zoomIcon = 'zoom-off'
      //   this.zoomTitle = '实际大小'
      // }
      this.zoomF = !this.zoomF
      if (this.zoomF) {
        this.adaptScreen()
      } else {
        this.acutalSize()
      }
    },
    //控制显示编辑视图
    showEditHandler() {
      this.isShowEdit = true
      setTimeout(() => {
        // 获取焦点
        this.$refs.inputVal.focus()
      }, 0)
    },
    //隐藏输入框
    hideEditHandler() {
      this.isShowEdit = false
    },
    //撤销
    undoHandler() {
      this.$emit('undo')
    },
    //重做
    redoHandler() {
      this.$emit('redo')
    },
    //隐藏保存作图记录
    hideSaveItemHandler() {
      this.isSaveListShow = false
      window.removeEventListener('click', this.hideSaveItemHandler)
    },
    // 显示保存作图记录
    showSaveItemHandler() {
      this.isSaveListShow = !this.isSaveListShow
      if (this.isSaveListShow) {
        setTimeout(() => {
          window.addEventListener('click', this.hideSaveItemHandler)
        }, 0)
      }
    },
    //保存
    async saveHandler() {
      let img = await this.$refs.print.printImageHandler() // 生成的是base64
      let data = this.toImage(img) // 转成对应类型图片的blob对象

      let url = await this.$blobUpload(
        data,
        new Date() + '.' + data.type.replace('image/', '')
      )
      let params = {
        goodsId: getter.goodsId() || '',
        ...getter.template(),
        productionPath: url.url || ''
      }
      let id = getter.id()
      id && (params['id'] = id)
      this.$axios.post(API.SAVE_API, params).then(data => {
        mitation.setId(data.data.id)
        this.$message({
          message: '保存成功',
          type: 'success',
          duration: 3 * 1000 // 显示时长(ms)
        })
      })
    },
    //保存作图记录
    saveRecordHandler() {
      this.$emit('saveRecord')
    },
    //放大
    zoominHandler() {
      if (getter.zoom() * 100 < 400) {
        mitation.setZoom(util.floatObj.add(getter.zoom(), 0.1))
      }
    },
    //缩小
    zoomoutHandler() {
      if (getter.zoom() * 100 > 15) {
        mitation.setZoom(util.floatObj.subtract(getter.zoom(), 0.1))
      }
    },
    hideLoginHandler(val) {
      if (val) {
        mitation.setLoginState(true)
      }
      this.showLogin = false
    },
    downloadHandler() {
      this.showLogin = true
      this.showDownload = !this.showDownload
    },
    hideDownloadHandler() {
      this.showDownload = false
    },
    setTitle(title) {
      mitation.setTitle(title)
    },
    toImage(data, type) {
      let arr = data.split(',')
      let mime = arr[0].match(/:(.*?);/)[1] || type
      // 去掉url的头，并转化为byte
      let bytes = window.atob(arr[1])
      // 处理异常,将ascii码小于0的转换为大于0
      let ab = new ArrayBuffer(bytes.length)
      // 生成视图（直接针对内存）：8位无符号整数，长度1个字节
      let ia = new Uint8Array(ab)
      for (let i = 0; i < bytes.length; i++) {
        ia[i] = bytes.charCodeAt(i)
      }
      return new Blob([ab], {
        type: mime
      })
    }
  },
  watch: {
    title(val) {
      this.setTitle(val)
    }
  }
}
</script>